body {
  margin: 0px; }

#demo {
  position: absolute;
  width: 100%;
  height: 100%; }
  #demo .ascii {
    display: inline-block;
    position: relative;
    width: 50%;
    height: 100%; }
    #demo .ascii > * {
      position: absolute;
      width: 100%;
      height: 100%; }
      #demo .ascii > *:first-child {
        opacity: 0; }
        #demo .ascii > *:first-child.show {
          opacity: 0.3; }
  #demo #settings {
    position: absolute;
    display: inline-block;
    top: 0px;
    left: 0px;
    width: 300px;
    margin: 20px;
    padding: 20px;
    background-color: #ffffff;
    box-shadow: 0px 0px 5px 0px #5f5f5f;
    z-index: 1; }
    #demo #settings .title {
      margin-bottom: 10px;
      text-align: center;
      font-size: 18px; }
    #demo #settings .field {
      height: 25px; }
      #demo #settings .field label {
        display: inline-block;
        width: 100px;
        font-size: 16px; }
      #demo #settings .field input {
        float: right;
        width: 50px;
        font-size: 16px;
        text-align: right; }
        #demo #settings .field input[type="checkbox"] {
          width: 15px;
          height: 15px; }
